"use client";

import React from "react";
import {
  EditorBlock,
  TextBlock,
  ImageBlock,
  VideoBlock,
  QuizBlock,
  // if InteractiveBlock exists in your types, keep this import:
  // otherwise it will just be `any` at runtime and still work.
  // @ts-ignore
  InteractiveBlock,
} from "@/lib/scorm/types";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { Checkbox } from "@/components/ui/checkbox";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { useLocale } from "@/hooks/use-locale";

interface PropertiesPanelProps {
  selectedBlock: EditorBlock | null;
  onBlockChange: (block: EditorBlock) => void;
}

export function PropertiesPanel({
  selectedBlock,
  onBlockChange,
}: PropertiesPanelProps) {
  const { t } = useLocale();

  // helper: always create a new block object (no mutation)
  const updateBlock = (partial: Partial<EditorBlock>) => {
    if (!selectedBlock) return;
    const updated: EditorBlock = { ...selectedBlock, ...partial };
    onBlockChange(updated);
  };

  /* ───────────────────────────── TEXT PANEL ───────────────────────────── */

  const renderTextPanel = () => {
    const block = selectedBlock as TextBlock;

    const handleHtmlChange = (
      e: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>,
    ) => {
      updateBlock({ html: e.target.value as any });
    };

    return (
      <div className="space-y-3">
        <Label htmlFor="text-html" className="text-xs font-medium">
          {t("scorm.props.block.text.html") || "Text content (HTML allowed)"}
        </Label>

        <textarea
          id="text-html"
          value={block.html || ""}
          onChange={handleHtmlChange}
          className="w-full min-h-[140px] rounded-md border border-slate-200 bg-slate-50 px-2.5 py-2 text-xs font-normal focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-sky-500"
          placeholder="<p>Write your text here...</p>"
        />

        <p className="text-[11px] text-muted-foreground">
          {t("scorm.props.block.text.help") ||
            "You can write normally, or use simple HTML like <p>, <strong>, <em>, <ul>, <li>."}
        </p>
      </div>
    );
  };

  /* ───────────────────────────── IMAGE PANEL ──────────────────────────── */

  const renderImagePanel = () => {
    const block = selectedBlock as ImageBlock;

    const handleSrcChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateBlock({ src: e.target.value } as any);
    };

    const handleAltChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateBlock({ alt: e.target.value } as any);
    };

    return (
      <div className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="image-src" className="text-xs font-medium">
            {t("scorm.props.block.image.src") || "Image URL"}
          </Label>
          <Input
            id="image-src"
            value={block.src || ""}
            onChange={handleSrcChange}
            className="h-8 text-xs"
            placeholder="https://example.com/image.png"
          />
          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.block.image.srcHelp") ||
              "Paste a full image URL or a path that is reachable by learners."}
          </p>
        </div>

        <div className="space-y-2">
          <Label htmlFor="image-alt" className="text-xs font-medium">
            {t("scorm.props.block.image.alt") || "Alternative text"}
          </Label>
          <Input
            id="image-alt"
            value={block.alt || ""}
            onChange={handleAltChange}
            className="h-8 text-xs"
            placeholder={
              t("scorm.props.block.image.altPlaceholder") ||
              "Short description for screen readers"
            }
          />
          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.block.image.altHelp") ||
              "Explain what is important in the image. This helps accessibility."}
          </p>
        </div>
      </div>
    );
  };

  /* ───────────────────────────── VIDEO PANEL ──────────────────────────── */

  const renderVideoPanel = () => {
    const block = selectedBlock as VideoBlock;

    const handleSrcChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateBlock({ src: e.target.value } as any);
    };

    return (
      <div className="space-y-2">
        <Label htmlFor="video-src" className="text-xs font-medium">
          {t("scorm.props.block.video.src") || "Video URL"}
        </Label>
        <Input
          id="video-src"
          value={block.src || ""}
          onChange={handleSrcChange}
          className="h-8 text-xs"
          placeholder="https://example.com/video.mp4"
        />
        <p className="text-[11px] text-muted-foreground">
          {t("scorm.props.block.video.help") ||
            "Use a direct link to an .mp4/.webm file or a streaming URL your player supports."}
        </p>
      </div>
    );
  };

  /* ───────────────────────────── QUIZ PANEL ───────────────────────────── */

  const renderQuizPanel = () => {
    const block = selectedBlock as QuizBlock;
    const options = block.options || [];

    const handleQuestionChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateBlock({ question: e.target.value } as any);
    };

    const handleOptionLabelChange = (index: number, value: string) => {
      const newOptions = options.map((opt, i) =>
        i === index ? { ...opt, label: value } : opt,
      );
      updateBlock({ options: newOptions } as any);
    };

    const handleOptionCorrectChange = (index: number, checked: boolean) => {
      const newOptions = options.map((opt, i) =>
        i === index ? { ...opt, correct: checked } : opt,
      );
      updateBlock({ options: newOptions } as any);
    };

    const handleAddOption = () => {
      const newOption = {
        id: `opt-${Date.now()}`,
        label:
          t("scorm.props.block.quiz.newOption") ||
          `Option ${options.length + 1}`,
        correct: options.length === 0, // first one correct by default
      };
      updateBlock({ options: [...options, newOption] } as any);
    };

    return (
      <div className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="quiz-question" className="text-xs font-medium">
            {t("scorm.props.block.quiz.question") || "Question"}
          </Label>
          <Input
            id="quiz-question"
            value={block.question || ""}
            onChange={handleQuestionChange}
            className="h-8 text-xs"
            placeholder={
              t("scorm.props.block.quiz.questionPlaceholder") ||
              "Write the question here"
            }
          />
        </div>

        <div className="space-y-2">
          <div className="flex items-center justify-between">
            <Label className="text-xs font-medium">
              {t("scorm.props.block.quiz.optionsLabel") || "Options"}
            </Label>
            <Button
              type="button"
              size="xs"
              variant="outline"
              className="h-7 px-2 text-[11px]"
              onClick={handleAddOption}
            >
              {t("scorm.props.block.quiz.addOption") || "Add option"}
            </Button>
          </div>

          <div className="space-y-1.5">
            {options.length === 0 && (
              <p className="text-[11px] text-muted-foreground">
                {t("scorm.props.block.quiz.noOptions") ||
                  "No options yet. Click “Add option” to create choices."}
              </p>
            )}

            {options.map((opt, index) => (
              <div
                key={opt.id}
                className="flex items-center gap-2 rounded-md border bg-muted/40 px-2.5 py-2"
              >
                <Input
                  value={opt.label}
                  onChange={(e) =>
                    handleOptionLabelChange(index, e.target.value)
                  }
                  className="h-8 text-xs flex-1"
                  placeholder={`${t("scorm.props.block.quiz.option") || "Option"} ${
                    index + 1
                  }`}
                />
                <div className="flex items-center gap-1">
                  <Checkbox
                    checked={!!opt.correct}
                    onCheckedChange={(checked) =>
                      handleOptionCorrectChange(index, Boolean(checked))
                    }
                    className="h-4 w-4"
                  />
                  <span className="text-[11px] text-muted-foreground">
                    {t("scorm.props.block.quiz.correct") || "Correct"}
                  </span>
                </div>
              </div>
            ))}
          </div>

          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.block.quiz.help") ||
              "Mark at least one option as correct. Learner answers will be checked against this."}
          </p>
        </div>
      </div>
    );
  };

  /* ────────────────────────── INTERACTIVE PANEL ───────────────────────── */

  const renderInteractivePanel = () => {
    const block = selectedBlock as InteractiveBlock;
    const style = block.style || {};

    const update = (partial: Partial<InteractiveBlock>) => {
      const merged = { ...block, ...partial } as any;
      onBlockChange(merged);
    };

    const updateStyle = (s: Partial<InteractiveBlock["style"]>) => {
      const mergedStyle = { ...(style || {}), ...s };
      update({ style: mergedStyle } as any);
    };

    const handleVariantChange = (
      e: React.ChangeEvent<HTMLSelectElement>,
    ) => {
      update({ variant: e.target.value as any });
    };

    const handleLabelChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      update({ label: e.target.value } as any);
    };

    const handleBodyChange = (
      e: React.ChangeEvent<HTMLTextAreaElement>,
    ) => {
      update({ bodyHtml: e.target.value } as any);
    };

    const handleCustomHtmlChange = (
      e: React.ChangeEvent<HTMLTextAreaElement>,
    ) => {
      update({ customHtml: e.target.value } as any);
    };

    const handleBgChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateStyle({ background: e.target.value });
    };

    const handleRadiusChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateStyle({ radius: e.target.value });
    };

    const handlePaddingChange = (e: React.ChangeEvent<HTMLInputElement>) => {
      updateStyle({ padding: e.target.value });
    };

    const handleShadowChange = (checked: boolean) => {
      updateStyle({ shadow: checked });
    };

    return (
      <div className="space-y-4">
        {/* Variant */}
        <div className="space-y-2">
          <Label className="text-xs font-medium">
            {t("scorm.props.block.interactive.variant") || "Type"}
          </Label>
          <select
            value={block.variant || "button"}
            onChange={handleVariantChange}
            className="h-8 w-full rounded-md border border-slate-200 bg-white px-2 text-xs"
          >
            <option value="button">
              {t("scorm.props.block.interactive.variant.button") ||
                "Button"}
            </option>
            <option value="callout">
              {t("scorm.props.block.interactive.variant.callout") ||
                "Callout / note"}
            </option>
            <option value="reveal">
              {t("scorm.props.block.interactive.variant.reveal") ||
                "Reveal / details"}
            </option>
            <option value="custom">
              {t("scorm.props.block.interactive.variant.custom") ||
                "Custom HTML"}
            </option>
          </select>
        </div>

        {/* Label */}
        <div className="space-y-2">
          <Label className="text-xs font-medium">
            {t("scorm.props.block.interactive.label") || "Label / title"}
          </Label>
          <Input
            value={block.label || ""}
            onChange={handleLabelChange}
            className="h-8 text-xs"
            placeholder="For example: Show answer"
          />
        </div>

        {/* Content based on variant */}
        {block.variant === "custom" ? (
          <div className="space-y-2">
            <Label className="text-xs font-medium">
              {t("scorm.props.block.interactive.customHtml") ||
                "Custom HTML"}
            </Label>
            <textarea
              value={block.customHtml || ""}
              onChange={handleCustomHtmlChange}
              className="w-full min-h-[120px] rounded-md border border-slate-200 bg-slate-50 px-2.5 py-2 text-xs"
              placeholder="<div>Write your HTML here...</div>"
            />
            <p className="text-[11px] text-muted-foreground">
              {t("scorm.props.block.interactive.customHelp") ||
                "Use this for advanced interactions (embeds, iframes, etc.)."}
            </p>
          </div>
        ) : (
          <div className="space-y-2">
            <Label className="text-xs font-medium">
              {t("scorm.props.block.interactive.body") ||
                "Body (rich text / HTML)"}
            </Label>
            <textarea
              value={block.bodyHtml || ""}
              onChange={handleBodyChange}
              className="w-full min-h-[120px] rounded-md border border-slate-200 bg-slate-50 px-2.5 py-2 text-xs"
              placeholder="<p>This content appears inside the callout / reveal.</p>"
            />
          </div>
        )}

        <Separator />

        {/* Simple style controls */}
        <div className="space-y-2">
          <Label className="text-xs font-medium">
            {t("scorm.props.block.interactive.style") || "Appearance"}
          </Label>
          <div className="grid grid-cols-2 gap-2">
            <div className="space-y-1">
              <span className="text-[11px] text-muted-foreground">
                Background
              </span>
              <Input
                value={style.background || ""}
                onChange={handleBgChange}
                className="h-8 text-[11px]"
                placeholder="#0ea5e9"
              />
            </div>
            <div className="space-y-1">
              <span className="text-[11px] text-muted-foreground">
                Radius
              </span>
              <Input
                value={style.radius || ""}
                onChange={handleRadiusChange}
                className="h-8 text-[11px]"
                placeholder="999px / 12px"
              />
            </div>
            <div className="space-y-1">
              <span className="text-[11px] text-muted-foreground">
                Padding
              </span>
              <Input
                value={style.padding || ""}
                onChange={handlePaddingChange}
                className="h-8 text-[11px]"
                placeholder="8px 16px"
              />
            </div>
            <div className="flex items-center gap-2 mt-4">
              <Checkbox
                checked={!!style.shadow}
                onCheckedChange={(checked) =>
                  handleShadowChange(Boolean(checked))
                }
                className="h-4 w-4"
              />
              <span className="text-[11px] text-muted-foreground">
                {t("scorm.props.block.interactive.shadow") ||
                  "Soft shadow"}
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  };

  /* ───────────────────────────── RENDER BODY ──────────────────────────── */

  if (!selectedBlock) {
    return (
      <div className="h-full flex flex-col items-center justify-center px-4 py-6 text-center">
        <div className="mb-3">
          <Badge
            variant="outline"
            className="text-[11px] px-2 py-0 rounded-full"
          >
            {t("scorm.props.title") || "Properties"}
          </Badge>
        </div>
        <p className="text-sm font-medium text-foreground mb-1">
          {t("scorm.props.emptyTitle") || "No block selected"}
        </p>
        <p className="text-[11px] text-muted-foreground max-w-xs">
          {t("scorm.props.desc") ||
            "Click any content block in the canvas to edit its properties here."}
        </p>
      </div>
    );
  }

  const typeLabelMap: Record<EditorBlock["type"], string> = {
    text: t("scorm.props.block.text.label") || "Text block",
    image: t("scorm.props.block.image.label") || "Image block",
    video: t("scorm.props.block.video.label") || "Video block",
    quiz: t("scorm.props.block.quiz.label") || "Quiz block",
    // @ts-ignore
    interactive:
      t("scorm.props.block.interactive.label") || "Interactive block",
  };

  const renderControls = () => {
    switch (selectedBlock.type) {
      case "text":
        return renderTextPanel();
      case "image":
        return renderImagePanel();
      case "video":
        return renderVideoPanel();
      case "quiz":
        return renderQuizPanel();
      // @ts-ignore
      case "interactive":
        return renderInteractivePanel();
      default:
        return (
          <p className="text-[11px] text-muted-foreground">
            {t("scorm.props.unsupported") ||
              "This block type does not have editable properties yet."}
          </p>
        );
    }
  };

  return (
    <div className="h-full flex flex-col bg-card/80">
      {/* Header */}
      <div className="px-4 pt-4 pb-3">
        <div className="flex items-center justify-between gap-2">
          <div className="space-y-1">
            <h3 className="text-sm font-semibold leading-none">
              {t("scorm.props.title") || "Block properties"}
            </h3>
            <p className="text-[11px] text-muted-foreground">
              {t("scorm.props.subtitle") ||
                "Adjust the selected element to match your learning design."}
            </p>
          </div>
          <Badge
            variant="secondary"
            className="text-[10px] px-2 py-0 rounded-full"
          >
            {typeLabelMap[selectedBlock.type] ?? selectedBlock.type}
          </Badge>
        </div>
      </div>

      <Separator className="mb-3" />

      {/* Body */}
      <div className="flex-1 overflow-y-auto px-4 pb-4 space-y-4 text-xs">
        {renderControls()}
      </div>
    </div>
  );
}
